/* Global */

.column {
  position: relative;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
  width: 100%;
  max-width: 1280px;  // HD ready
  margin: 0px auto;
  justify-content: center;
  padding: 36px 24px;
}

.fullheight {
  min-height: calc(100vh - var(--nav-full-height));
}

.underline {
  &::after {
      display:block;
      position: relative;
      left: calc(50% - 25px);
      content:'';
      height: 20px;
      width: 50px;
      border-bottom: 1px solid #5761B2;
    }
}

.underline-start {
  &::after {
      display:block;
      position: relative;
      content:'';
      height: 20px;
      width: 50px;
      border-bottom: 1px solid #5761B2;
    }
}

.flex {
  display: flex;
  flex-wrap: wrap;
}

.button {
  display: block;
  background-color: white;
  color: #3B93AD;
  width: 250px;
  height: 40px;
  line-height: 42px;
  font-weight: 600;
  text-align: center;
  margin-right: 2em;
  margin-top: 10px;
}

.purple {
  color: white;
  background-color: #5761B2;
}

.bg {
  position: absolute;
  height: 100%;
  width: 100%;
  left: 0;
  top: 0;
  z-index: -10;
  overflow: hidden;
}

@mixin bg-text($content, $positionY) {
  &::after {
    content: $content;
    font-family: var(--font-head);
    font-size: 475px;
    font-weight: bold;
    color: #F9F9F9;
    position: absolute;
    bottom: $positionY;
    left: 50%;
    transform: translateX(-50%);
    z-index: -9;
  }
}

  @mixin bg-item($top, $left, $opacity: 0.25, $rotate: 0, $scale: 1) {
  position: absolute;
  left: $left;
  top: $top;
  z-index: -3;
  opacity: $opacity;
  transform: rotate($rotate) scale($scale);
  filter: drop-shadow(10px 10px 10px rgba(0, 0, 0, .4));
}

.text-center {
  text-align: center;
}

.mb-1 {
  margin-bottom: 1em;
}
