@import url('https://fonts.googleapis.com/css2?family=Fira+Sans&family=Open+Sans:wght@400;600;700&display=swap');

:root {
  --text-color: #323232;
  --h-color: #CEE4EB;

  --nav-background: #FFFFFF;
  --background: #E5E5E5;


  --font-head: "Fira Sans", sans-serif;
  --font-body: "Open Sans", sans-serif;
  --font-size: 16px;

  --nav-full-height: 120px;
  --nav-mobile-height: 55px;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
}

body {
  color: var(--text-color);
  margin: 0;
  font-family: var(--font-body);
  font-size: var(--font-size);
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}

section, footer {
  position: relative;
}

a {
  text-decoration: none;
}

svg {
  margin: 0;
}

/* Typography */

h1,
h2,
h3,
h4 {
  font-family: var(--font-head);
  line-height: 1.2;
  padding: 0;
  margin: 0;
}

h1 {
  font-size: 3.75rem;
}

h2 {
  font-size: 2.5rem;
  font-weight: 700;
}

h3 {
  font-size: 1.5rem;
  font-weight: 600;
  font-family: var(--font-body);
}

h4 {
  font-size: 1.125rem;
  margin: 1em 0;
}

p {
  font-style: normal;
  font-weight: normal;
  line-height: 1.375;
  margin: 0 0 1.5em 0;
}


/* Main */

.main {
  display: flex;
  background: linear-gradient(180deg, #6CAEC1 0%, #3B93AD 100%);
  align-items: center;
  transition: ease 0.5s;
}

.quote {
  position: absolute;
  top: -5%;
  left: -5%;
  transition: ease .5s;
}

.main {
  h2, h3 {
    color: var(--h-color);
  }

  h2 {
    z-index: 1;
    text-align: right;
    letter-spacing: -1px;
    margin-right: 1.3em;
    &::before {
      position: relative;
      right: -100%;
      content: "";
      display: block;
      width: 50px;
      height: 50px;
      border-top: 15px solid var(--h-color);
      border-right: 15px solid var(--h-color);
    }
  }

  .author {
    margin-bottom: 2em;
  }
}

.testimonial {
  margin-bottom: 3em;
  text-transform: uppercase;
}

/* Business domain */

.business-domain {
  text-align: center;
}

.header {
  margin-top: 3em;
  p {
    margin: 2em 0 2em 0;
  }
}

.capabilities {
  display: flex;
  justify-content: space-between;
  margin-bottom: 5em;
  flex-wrap: wrap;
}

.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  max-width: 340px;
  min-width: 300px;
  margin: 1em 1em;
  @media (max-width: 359px) {
    min-width: 0;
  }
}

.icon-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 75px;
  height: 75px;
  border: 1.5px solid #5761B2;
  border-radius: 5px;
}

/* Tech stack */

.tech-stack-container {
  display: flex;
  align-items: stretch;
  margin: 2.5em 0;
}

.stack {
  display: flex;
  flex: 1 1 200px;
  flex-direction: column;
}

.tech-card {
  display: flex;
  flex: 1;
  justify-content: center;
  background: #FFFFFF;
  box-shadow: 2px 2px 5px rgba(0, 0, 0, 0.15);
  border-radius: 5px;
  min-height: 70px;
  margin: 7px;

  img {
    object-fit: none;
  }
}

.bg-possibilities {
  @include bg-text('możliwości', -125px);
}

/* Contact us / Quote */

.inspire {
  display: flex;
  background: linear-gradient(94.08deg, #5761B2 0%, #1FC5A8 100%);
  opacity: 0.5;
  height: 350px;
}

.inspire-header {
  color: white;
  margin-bottom: 2em;
  font-weight: bold;

  span {
    display: block;
    font-size: 40px;
  }

  .large-text {
    font-size: 60px;
  }
}

/* General */

.schedule-meeting-button {
  a {
    padding: 12px 25px;
    border: 1px solid black;
  }
}

/* Portfolio */

.portfolio-wrapper {
  display: flex;
  justify-content: space-around;
  margin: 3em 0;
  .chevron {
    position: absolute;
    cursor: pointer;
    background: #E1E1E1;
    width: 40px;
    height: 40xp;
    top: 230px;
    &.left {
      left: 20px;
    }
    &.right {
      right: 20px;
    }
  }
}


.project-content {
  display: flex;
  flex-direction: column;
  a {
    &:last-child {
      margin-top: auto;
    }
  }
}

.slider {
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.15);
}

.lg-slider {
  height: 500px;
  flex: .45;
  margin-right: 3em;
  position: relative;
}

.small-slider {
  display: none;
}

.project-description {
  margin-bottom: 1em;
  line-height: 1.2;
  &:last-child {
    margin-bottom: 8em;
  }
  a {
    font-weight: bold;
    color: initial;
    text-decoration: underline;
  }
}

/* Clients */

.clients {
  margin: 2em 0;
  text-align: center;
}

.client-logo-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 1em 0;
}

.client-logo-list {
  div:nth-child(5) {
    svg {
      height: 60px;
    }
  }
}

.client-logo {
  svg {
    height: 75px;
    margin: 1em 2em;
  }
}

/* Background svg */

.bg-pattern {
  background: url(../images/pattern.svg) repeat-y;
  width: 490px;
  height: 100%;
  position: absolute;
  right: 0%;
  z-index: -1;
  mask-image: linear-gradient(67deg, rgba(2,0,36,0) 0%, rgba(0,0,0,0) 25%, rgba(0,0,0,1) 100%);
  -webkit-mask-image: linear-gradient(67deg, rgba(2,0,36,0) 0%, rgba(0,0,0,0) 25%, rgba(0,0,0,1) 100%);
}

.bg-projects {
  @include bg-text('projekty', -100px);
}

.bg-plus-1 {
  @include bg-item(3%, 14%);
}

.bg-plus-2 {
  @include bg-item(16%, 78%);
}

.bg-plus-3 {
  @include bg-item(42%, 51%);
}

.bg-arrow-1 {
  @include bg-item(56%, 31% , .12);
}

.bg-arrow-2 {
  @include bg-item(8%, 80%, .12, 180deg);
}

.sm-slider {
  display:none;
}

/* Media queries */

@media (max-width: 1700px) {
  .quote {
    transform: scale(.66);
  }
}

@media (max-width: 1280px) {
  .quote {
    transform:scale(.5) translate(50px, 20px);
  }
}

@media (max-width: 1180px) {
  .tech-stack {
    margin: 0 auto;
  }
  .tech-stack-container {
    flex-direction: column;
  }
  .stack {
    flex-direction: row;
    flex: 1;
  }

  .client-logo {
  margin: 1em 1.2em;
  }

  .capabilities {
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width:959px) {
  //.menu {
  //  display: none;
  // }
  // hamburger menu to do
}

@media (max-width:768px) {
  .quote {
    transform: scale(.5) translate(-20px, -50px);
  }
  .main {
    h2 {
      font-size: 2rem;
      margin-right: 0;
      &::before {
        display:none;
      }
    }
  }

  .tech-card {
    min-height: 50px;
    height: 50px;
    margin: 5px;
    img {
      height: 100%;
      width: 100%;
      transform: scale(0.6);
      object-fit: contain;
    }
  }

  .bg-pattern {
    display: none;
  }

  .portfolio-wrapper {
    flex-direction: column;
  }

  .project-content {
    text-align: center;
    h2.underline-start {
      &::after{
        left: calc(50% - 25px);
      }
    }

    .project-description {
      text-align: justify;
    }
  }

  .client-logo-list {
    div:nth-child(5) {
      svg {
        height: 40px;
      }
    }
  }

  .client-logo {
    svg {
      height: 50px;
      margin: 0;
    }
  }

  .inspire {
    height: auto;
  }

  .inspire-header {
    span {
      font-size: 30px;
    }

    .large-text {
      font-size: 40px;
    }
  }

  #testimonial-swiper .column {
    padding-bottom: 0;
  }
}

@media (max-width:510px) {
  .quote {
    top: 0;
  }
  h2 {
    font-size: 1.5rem;
    line-height: 1.5;
  }
  .main {
    h2, h3 {
      text-align: center;
    }
    h3 {
      font-size: 1rem;
      font-weight: 400;
      line-height: 2rem;
    }
  }
  .icon-wrapper {
    transform: scale(.66);
  }
  .capabilities {
    margin-bottom: 2em;
  }

  .inspire-header {
    margin: 0 0 1em 0;
    span {
      font-size: 20px;
    }

    .large-text {
      font-size: 28px;
    }
  }

  .button {
    width: 100%;
    margin-right: 0;
  }

  .clients {
    margin: 0;
  }

  .client-logo-list {
    div:nth-child(5) {
      svg {
        height: 20px;
      }
    }
  }

  .client-logo {
    svg {
      height: 30px;
      margin: 0;
    }
  }
}

.leaflet-interactive {
  cursor: initial;
}

#projects-swiper {
  max-width: 100%;
}

// tablet
@media (min-width: 640px) and (max-width: 1180px) {
  .capabilities .card {
    max-width: none;
  }
  .tech-stack {
    margin: 0;
  }
}

