.about-header {
  position: relative;
  display: flex;
  height: 215px;
  color: #E7F1F5;
  background: url(../images/about-header.png) ;
  text-align: center;
  h1 {
    &::after {
      border-bottom: 1px solid #E7F1F5;
    }
  }
}

.header-bg {
  display: flex;
  position: absolute;
  width: 100%;
  height: 100%;
  background: #3B93AD;
  opacity: 0.85;
}

.article {
  margin: 3em 0;
  h2 {
    margin-bottom: 1.875rem;
  }
  img {
    width: 500px;
    height: auto;
    float: right;
    margin: 0 0 2.5em 2.5em;
  }
}

.bg-were {
  @include bg-text('jesteśmy', -126px);
}

/* Statistics */

.statistics {
  background: linear-gradient(100.08deg, #5761B2 0%, #1FC5A8 100%);
  opacity: 0.5;
  color: white;
  padding: 3em 0 2em 0;
}
  

.stats-row {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  flex-wrap: wrap;

  // Temporary solution - not responsive
    &:first-of-type {
    margin-bottom: 100px;
  }
}

.stat-item {
  flex: 0.5;
  text-align: center;
  font-weight: bold;
  span {
    display: block;
    margin: 10px 0;
    text-transform: uppercase;
  }
}

.stat {
  font-family: var(--font-head);
  font-size: 40px;
  font-weight: bold;
}

.infinity {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 54px;
}

/* Values */

.values {
  padding: 3em 0 2em 0;
  h2 {
    text-align: center;
  }
}

.values-list {
  display: flex;
  flex-wrap: wrap;
  margin: 40px 0;
}

.values-item {
  display: flex;
  width: 50%;
}

.values-icon-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 40px;
  width: 40px;
  height: 40px;
  border: 1.5px solid #5761B2;
  border-radius: 5px;
  margin-right: 20px;
}

.values-item-content {
  h4 {
    margin-top: 0;
  }
  padding-right: 40px;
}

.bg-values {
  @include bg-text('wartości', -126px);
}

/* Location */

.location-section {
  background-color: #F9F9F9;
  padding: 76px 0;
}

.location {
  display: flex;
}

.location-photo {
  width: 500px;
  height: 492px;
  padding-right: 50px;
}

.location-content {
  h2 {
    margin-bottom: .6em;
  }

  ul {
    padding: 0 0 0 20px;
    li {
      list-style-image: url(../../assets/images/about-list-style.svg);
      padding: 10px 0 10px 20px;
    }
  }
}

/* Team */

.team-section {
  padding: 100px 0;
  h2 {
    text-align: center;
  }
}

.team {
  padding: 40px 0;
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-template-rows: repeat(4, 254px);
  gap: 50px;
}

.team-member {
  background-color: #ccc;
}

.k {
  grid-area: 1 / 1 / 3 / 2;
}

.p {
  grid-area: 1 / 2 / 3 / 3
}

.bg-values {
  @include bg-text('zespół', -126px);
}