.header-navigation {
  position: fixed;
  height: var(--nav-full-height);
  min-height: 60px;
  width: 100%;
  background-color: #FFFFFF;
  z-index: 10;
  box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.1);
}

.header-navigation-wrapper {
  display: flex;
  flex: 1 1 auto;
  flex-direction: row;
  justify-content: space-between;
  height: 100%;
  width: 100%;
  max-width: 1280px; // HD ready
  margin: 0px auto;
  padding: 0 24px;
}

.logo {
  margin-right: 50px;
  display: flex;
  justify-content: flex-start;
  flex-direction: row;
  align-items: center;
}

.header-navigation-wrapper .schedule-meeting-button {
  margin-left: 50px;
}

nav {
  display: flex;
  align-items: center;
  flex-grow: 1;
  flex-shrink: 0;

  a {
    color: var(--text-color);

    &:hover {
      color: #80deea;
    }
  }

  .active {
    color: #80deea; 
  }
  
}

.item {
  flex-direction: row;
  display: none;
  position: relative;
  bottom: -1px;
  height: 100%;
  align-items: center;
  margin-right: 24px;
}

.languages {
  margin-left: auto;
  @media (max-width: 420px) {
    position: fixed;
    right: 12px;
    bottom: 10px;
    background: white;
    border-radius: 6px;
    border: 1px solid black;
    height: 48px;
    width: 48px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

@media (max-width:768px) {
  .header-navigation {
    height: var(--nav-mobile-height);
  }
  .main {
    margin-top: var(--nav-mobile-height);
  }

  .logo {
    svg {
      height: 30px;
      width: 150px;
    }
  }
}

@media (min-width:769px) {
  .main {
    margin-top: var(--nav-full-height);
  }
}

@media (max-width:959px) {
  //.menu {
  //  display: none;
  // }
  // hamburger menu to do
}

@media (max-width:460px) {
  .header-navigation-wrapper .schedule-meeting-button {
    margin-right: 0;
    margin-left: auto;

    a {
      padding: 6px 12px;
      border: 1px solid black;
      font-size: 10px;
    }
  }
  
  .logo {
    margin-right: 20px;
  }

  // hamburger menu to do
}

@media (max-width: 359px) {
  .header-navigation-wrapper .schedule-meeting-button {
    a {
      padding: {
        left: 4px;
        right: 4px;
      }
    }
  }
}

