@mixin footer-icon($icon) {
  padding-left: 36px;
  &::before {
    display: block;
    content: '';
    background-image: url($icon);
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
    height: 20px;
    width: 20px;
    position: absolute;
    transform: translate(-36px, 5px);
  }
}

footer {
  color: white;
  background-color: black;
  z-index: 0;
  background: url('../images/zabrze.jpg');
  background: {
    position: top center;
    size: cover;
  }
  a {
    transition: 200ms color;
    &:hover {
      color: white !important;
    }
  }
}

.footer-wrapper {
  display: flex;
  width: 100%;
  max-width: 1280px; // HD ready
  margin: 0 auto;
  padding: 0 24px;
  @media (max-width: 1180px) {
    .schedule-meeting-button {
      display: none !important;
    }
  }
}

.footer-contact-wrapper {
  position: relative;
  flex: .6;
  &::after {
    position: absolute;
    top: 10%;
    right: -5%;
    content: "";
    display: block;
    width: 90px;
    height: 90px;
    border-top: 25px solid #989898;
    border-right: 25px solid #989898;
  }
}

.footer-contact {
  margin-top: 4em;
  display: flex;
  flex-wrap: wrap;
}

.footer-contact-info {
  color: #989898;
  font-style: normal;
  line-height: 2;
  margin: 0 4em 1.5em 0;
  h4 {
    color: white;
  }
  a {
    color: inherit;
  }
}

.social-media .schedule-meeting-button {
    display: inline-block;
    border: 1px solid white;
    padding: 12px 0;
    a {
      color: #989898;
      border: none;
      margin-right: 0;
    }
    @media (max-width: 915px) {
      margin-top: 16px;
    }
  }

.leaflet-pane .leaflet-tile-pane {
  filter: invert(1) sepia(100%) hue-rotate(-230deg);
}

.leaflet-container .leaflet-control-attribution {
  color: rgba(255, 255, 255, 0.8);
  background-color: rgba(0, 0, 0, 0.8);

  a {
    color: rgba(255, 255, 255, 0.8);
  }
}

/* Address items */

.footer-contact-person {
  @include footer-icon('../images/icons/person-icon.svg');
}

.footer-contact-phone {
  @include footer-icon('../images/icons/phone-icon.svg');
}

.footer-contact-mail {
  @include footer-icon('../images/icons/mail-icon.svg');
}

.footer-address {
  @include footer-icon('../images/icons/location-pin-icon.svg');
}

/* Social media */

.social-media {
  margin: 1em 0 3em 0;
  a {
    margin-right: 3em;
  }

  @media (max-width: 1100px) {
    width: 100%;
    text-align: center;
    flex-wrap: wrap;
    justify-content: flex-start;
    display: flex;
  }
}

