//   flex: .4;
// }

/* Media queries */

@media (max-width:1180px) {
  .footer-contact-wrapper {
    flex: 0.5;
    &::after {
      transform: scale(.66);
      top: 5%;
    }
  }
  #footer-map {
    flex: 0.5;
  }
}

@media (max-width: 768px) {
  .footer-contact-wrapper {
    flex: 1;
    &::after {
      transform: scale(.5);
    }
  }
  #footer-map {
    display: none;
    flex: 0 0;
  }
}

@media (max-width: 400px) {
  .footer-contact-wrapper::after {
    right: 5%;
  }
}