.swiper-slide {
  display: flex;
  .column {
    width: 90%;
    @media (max-width: 640px) {
      margin: 0;
      padding: {
        left: 10px;
        right: 10px;
      }
    }
  }
}

.swiper-pagination-bullet {
  width: 12px;
  height: 12px;

  &.swiper-pagination-bullet-active {
    background-color: var(--h-color);
  }
}

.portfolio-wrapper {
  .swiper-container {
    .swiper-img {
      width: 700px;
      height: 498px;
      margin-right: 32px;
    }
  }

  @media (max-width: 1000px) {
    .swiper-container {
      .swiper-slide {
        display: block;
      }
      .swiper-img {
        width: 100%;
        height: auto;
      }
      .project-description:last-child {
        margin-bottom: 3em;
      }
    }
  }
  
}